
<template>
  <div   :id="chartid" style="width: 100%;height: 100%;margin: 0 auto;"></div>
</template>

<script>
import Axios from "axios";
export default {
name: "toolsNetworkVisualization",
  props:['graphData',"plotconfig"],
  data() {
    return {
      myChart: null,
      graph: null,
      nodeWithID:{},
      chartid:"thisChartCancerTissue"
    }

  },
  // computed:{
  //   nodeWithID(){
  //     // node n1 hover edge
  //
  //   }
  //
  // },
  mounted() {

    if(this.graphData != null){
      this.createIDNode();
      this.drawEcharts()
    }


  },
  watch:{
    graphData:function(){
      if(this.graphData != null){
        this.createIDNode();
        this.drawEcharts()
      }
    },
    plotconfig:{
     handler:function (newval){

       // console.log(this.graphData)
       if(this.graphData != null){
         this.createIDNode();
         this.drawEcharts()
       }

     },
      deep:true,
      immediate:true,
    }
  },
  methods:{
    createIDNode(){
      // let idnode = {}
      // console.log("aaa")
      this.nodeWithID = {};
      this.graphData && this.graphData.nodes && this.graphData.nodes.forEach(item=>{
        this.nodeWithID[item.id] = item.name;
      })

      // return idnode;


    },
    drawEcharts() {



      let chartDom = document.getElementById(this.chartid);
      let myChart = this.$echarts.init(chartDom);
      let option;

      myChart.showLoading();


        this.graph = this.graphData
        that = this;

      // this.graph.nodes.forEach(function (node) {
      //     node.symbolSize = 15;
      //
      //   });

        option = {
          toolbox: {
            feature: {
              saveAsImage: {
                // type:"svg",
                title: "save",
                name:"MACdb_Network_visualization"
              }
            }
          },


          title: {
            text: 'MACdb association network',
            subtext: 'Default layout',
            top: 'bottom',
            left: 'right'
          },
          tooltip: {
            formatter: function (params){
                // console.log(params)

              if(params.dataType == "edge"){
                //thatthis eachartthis
                return `${ that.nodeWithID[params.data.source] } > ${ that.nodeWithID[ params.data.target] } : <b> ${params.data.value} </b>`
              }else if(params.dataType == "node"){
                  return `${params.data.name}`
              }

            }

          },
          legend: [
            {
              // selectedMode: 'single',
              orient:"vertical",
              left:"10",
              top:"10",
              itemGap:25,
              data: this.graph.categories.map(function (a) {
                return a.name
              })
            }
          ],

          animation:false,
          series: [
            {
              cursor:"pointer",
              animation:false,
              // name: 'MACdb association network',
              type: 'graph',
              layout: 'force',
              // layout: 'none',
              // layout: 'none',
              // layout: 'circular',
              data: this.graph.nodes,
              links: this.graph.links,
              categories: this.graph.categories,
              roam: true,
              label: {
                show: this.plotconfig.showLabel,
                position: 'middle',
                formatter: '{b}'
              },
              draggable: true,
              animationEasing:"linear",
              zoom:3,
              force: {
                edgeLength: 15,
                repulsion: 20,
                gravity: 0.1,
                friction:0.1,

              }
            }
          ]
        };


      myChart.clear()
      option && myChart.setOption(option);



      myChart.hideLoading();

      let that = this
      myChart.off('click')
      myChart.on('click', function(params) {
        // console.log("clicking....")
        if(params.dataType == "node"){
          let myattr = params.data.myattrs

          if(myattr.type == "tid"){
            that.$commonfunc.openAtNewPageTraitDetail(myattr.id)
          }
          else if(myattr.type == "cid"){

            that.$commonfunc.openAtNewPageMetaboliteDetail(myattr.id)
          }
        }

      });

      window.addEventListener("resize", () => {
        // echartsresizeecharts
        that.myChart && that.myChart.resize();
        // echartsechartsresize,.vueecharts
        /*
        this.myChart2.resize();
        this.myChart3.resize();
        ......
        */
      });
    }
  },

  beforeDestroy() {
    /* resize
    CPUGPU*/
    window.removeEventListener("resize", () => {
      this.myChart.resize();
    });
  },

}
</script>

<style scoped>

</style>



