<template>
  <div   :id="chartid" style="width: 95%;height: 90%;margin: 0 auto;"></div>
</template>

<script>
import Axios from "axios";
export default {
name: "top10Trait",
  data() {
    return {
      myChart: null,
      dat: [],
      chartid:"thisChartTop10Trait"
    }

  },
  mounted() {

    this.loadData();
    // this.drawEcharts();

  },
  methods:{
    loadData() {
      Axios.post(
        baseURL + "/api/get_stats_data_total",
      ).then(res => {
        this.dat = res.data;

            this.drawEcharts();

      })
  },
    drawEcharts() {

      let that = this
      let chartDom = document.getElementById(this.chartid);
      that.myChart = this.$echarts.init(
        chartDom,
      );

      let option ={
        animationDuration: 2000,

        // 
        toolbox: {
          feature: {
            saveAsImage: {
              // type:"svg",
              title: "save",
              name:"MACdb_statistics_top_traits"
            }
          }
        },
        grid:{

          bottom:"35%"
        },
        tooltip:{
          trigger:'item'
        },
        dataset: [
          {
            id: 'dataset_cancertype_count',
            source: this.dat.toptrait_count,
            sourceHeader:true,
          },
        ],
        xAxis: {

          inverse:false,
          type: 'category',
          nameLocation: 'middle',
          axisTick:{
            show:false,
          },
          axisLabel:{
            inside:false,
            rotate: 45
          }
        },
        yAxis: {
          name:"Number of Study",
          nameLocation:"middle",
          nameGap:35,
        },

        series: [
          {
            datasetIndex:0,
            type: 'bar',
            smooth:true,
            label:{
              show:true,
              position: 'top'
            },
            encode: {
              //  "amount"  X 
              x: 'Trait_Ontology',
              //  "product"  Y 
              y: 'count'
            },

          },


        ]

      };
      that.myChart.setOption(option);

      that.myChart.on('click', function(params) {
        // if(params.dataType == "node"){
        //   let myattr = params.data.myattrs
        //
        //   if(myattr.type == "tid"){
        //     that.$commonfunc.openAtNewPageTraitDetail(myattr.id)
        //   }
        //   else if(myattr.type == "cid"){

        that.$commonfunc.openAtNewPageTraitDetail(params.data[1])
        //   }
        // }

      });


      window.addEventListener("resize", () => {
        // echartsresizeecharts
        that.myChart.resize();
        // echartsechartsresize,.vueecharts
        /*
        this.myChart2.resize();
        this.myChart3.resize();
        ......
        */
      });
    }
  },
  beforeDestroy() {
    /* resize
    CPUGPU*/
    window.removeEventListener("resize", () => {
      this.myChart.resize();
    });
  },

}
</script>

<style scoped>

</style>



